import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { AlertService } from '@nequi/features/commons/presentation';
import {
  ModuleConfigProvider,
  NavigatorProvider,
  TranslateProvider
} from '@nequi/features/commons/providers';
import { ProfileService } from '@nequi/features/profile/presentation';
import { CardModalService, FirstTimeMessageBottomSheetService } from '@nequi/ui';

import { SearchInfo } from '../interfaces/search-info.interface';
import { CodensaConfig } from './home.config';

@Injectable()
export class HomeService {
  public config = CodensaConfig;
  public isWrongTime = false;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private translateProvider: TranslateProvider,
    private applicationDataInteractor: ApplicationDataInteractor,
    private route: ActivatedRoute,
    private cardModalService: CardModalService,
    private alertService: AlertService,
    private profileService: ProfileService,
    private moduleConfigProvider: ModuleConfigProvider
  ) {}

  public goBack() {
    this.navigatorProvider.back();
  }

  public async showCodensaWelcome(isWrongTime: boolean = false) {
    const { button, cssClass } = this.config.firstMessage;

    const isFirstTime = await this.applicationDataInteractor.findValue<boolean>(
      this.moduleConfigProvider.applicationData.isFirstTime,
      true
    );

    if (isFirstTime) {
      const bottomSheetMessages = this.translateProvider.get(this.config.i18n.bottomSheet.welcome);

      await this.firstTimeMessageBottomSheetService.show({
        icon: bottomSheetMessages.image,
        message: bottomSheetMessages.message,
        content: bottomSheetMessages.content,
        cssClass: cssClass.firstTimeMessage,
        header: {
          view: true,
          action: () => this.redirect(true),
        },
        button: {
          label: bottomSheetMessages.button,
          color: button.color,
          fill: button.fill,
          action: () => this.redirect(isWrongTime),
        },
      });

      await this.applicationDataInteractor.save(
        this.moduleConfigProvider.applicationData.isFirstTime,
        false
      );
    }
  }

  public async onShowCardModal(
    i18nMessage: string,
    onAccept?: Function,
    onCancel?: Function
  ): Promise<void> {
    const message = this.translateProvider.get(i18nMessage);
    const buttons = message.buttons?.cancel
      ? [
          {
            text: message.buttons.accept,
            handler: async () => onAccept?.(),
          },
          {
            text: message.buttons.cancel,
            color: this.config.colors.midnight,
            handler: async () => onCancel?.(),
          },
        ]
      : [
          {
            text: message.buttons.accept,
            handler: async () => onAccept?.(),
          },
        ];

    await this.cardModalService.show({
      title: message.header,
      imgPath: message.image,
      content: message.content,
      buttons: buttons,
    });
  }

  public async onShowError(messages: string) {
    const alertConfig = this.translateProvider.get(messages);

    this.alertService.show({
      messages: {
        message: alertConfig.content,
        header: alertConfig.header,
        buttons: alertConfig.buttons,
      },
    });
  }

  public goToRoute(route: string) {
    this.navigatorProvider.navigateRoot(route, {
      relativeTo: this.route,
      replaceUrl: true,
    });
  }

  public async setInfoSearch(data: SearchInfo) {
    await this.applicationDataInteractor.save(
      this.moduleConfigProvider.applicationData.searchBy,
      JSON.stringify(data)
    );
  }

  public async getInfoSearch() {
    const searchBy = await this.applicationDataInteractor.findValue<string>(
      this.moduleConfigProvider.applicationData.searchBy
    );

    return searchBy
      ? (JSON.parse(searchBy) as SearchInfo)
      : ({ email: this.profileService.getEmail() } as SearchInfo);
  }

  private redirect(isWrongTime: boolean) {
    this.firstTimeMessageBottomSheetService.hide();

    if (isWrongTime) {
      this.goBack();
    }
  }
}
