import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { CodensaInteractor, CodensaRepository } from '@nequi/features/codensa/core';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { QrReaderService } from '@nequi/features/commons/presentation';
import { FeaturesMoneyManagementModule } from '@nequi/features/money-management/presentation';
import { NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { CodensaConfig } from '../config/codensa.config';
import { FeaturesCodensaRoutingModule } from './features-codensa-routing.module';
import { BillListOrganism } from './organisms/bill-list/bill-list.organism';
import { HomePage } from './pages/home/home.page';

@NgModule({
  declarations: [HomePage, BillListOrganism],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    FeaturesCodensaRoutingModule,
    NequiUiModule,
    FeaturesMoneyManagementModule,
  ],
  providers: [
    {
      provide: ModuleConfigProvider,
      useValue: CodensaConfig,
    },
    QrReaderService,
    {
      provide: CodensaInteractor,
      deps: [CodensaRepository],
      useFactory: (repository: CodensaRepository) => new CodensaInteractor(repository),
    },
  ],
})
export class FeaturesCodensaModule {}
