import { CurrencyPipe, DatePipe } from '@angular/common';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SearchByType } from '@nequi/features/codensa/core/constants';
import { BarcodeEntity, BillEntity, GetBillEntity } from '@nequi/features/codensa/core/entities';
import { ConfirmInfoPocket, ConfirmInfoPocketItem } from '@nequi/ui';
import { toCapitalize } from '@nequi/utils/string';

import { SearchInfo } from '../interfaces/search-info.interface';
import { CodensaConfig } from './home.config';

export class HomeViewModel {
  public config = CodensaConfig;

  public form: FormGroup;
  public accountNumber: FormControl;
  public email: FormControl;
  public pocket: FormControl;
  public billInfo: BillEntity;

  public pocketList = [];
  public queryBarCode: string;

  public async getBillsData(): Promise<GetBillEntity> {
    const barCode = this.queryBarCode
      ? String(
          this.queryBarCode.substring(
            this.config.validators.barCode.fromValue,
            this.config.validators.barCode.toValue
          )
        )
      : null;

    if (this.accountNumber.value === barCode) {
      return {
        searchBy: SearchByType.BARCODE,
        billCode: this.queryBarCode,
      } as GetBillEntity;
    }

    return {
      searchBy: SearchByType.ACCOUNT_NUMBER,
      billCode: this.accountNumber.value,
    } as GetBillEntity;
  }

  public async setAccountNumber(params?: BarcodeEntity) {
    this.queryBarCode = params?.barcode ?? null;
    this.accountNumber.setValue(params?.accountNumber ?? null);
  }

  public buildItems(
    messages: Record<string, string>,
    billType: string,
    currencyPipe: CurrencyPipe,
    datePipe: DatePipe
  ): Array<ConfirmInfoPocketItem | ConfirmInfoPocket> {
    return [
      {
        label: messages.type,
        value: billType,
      },
      {
        label: messages.amount,
        value: currencyPipe.transform(this.billInfo.amount, '', 'symbol', '.0'),
      },
      {
        label: messages.accountNumber,
        value: this.billInfo.accountNumber,
      },
      {
        label: messages.customerName,
        value: toCapitalize(this.billInfo.customerName),
      },
      {
        label: messages.address,
        value: toCapitalize(this.billInfo.address),
      },
      {
        label: messages.expirationAt,
        value: datePipe.transform(this.billInfo.expirationAt),
      },
      {
        label: messages.email,
        value: this.email.value,
      },
    ];
  }

  public buildForm(infoSearch: SearchInfo) {
    this.createControls(infoSearch);
    this.form = new FormGroup({
      accountNumber: this.accountNumber,
      email: this.email,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  private createControls(infoSearch: SearchInfo) {
    const { accountNumber, email } = infoSearch;

    this.accountNumber = new FormControl(accountNumber ?? null, [
      Validators.required,
      Validators.minLength(this.config.validators.accountNumber.minLength),
      Validators.maxLength(this.config.validators.accountNumber.maxLength),
      Validators.pattern(this.config.validators.accountNumber.pattern),
    ]);

    this.email = new FormControl(email ?? null, [
      Validators.required,
      Validators.minLength(this.config.validators.email.minLength),
      Validators.maxLength(this.config.validators.email.maxLength),
      Validators.pattern(this.config.validators.email.pattern),
    ]);

    this.pocket = new FormControl(this.pocketList[0]);
  }
}
