import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { CodensaInteractor, CodensaRepository } from '@nequi/features/codensa/core';
import { BarcodeEntity, BillEntity } from '@nequi/features/codensa/core/entities';
import {
  BillWithPaymentInTransitException,
  InvalidBarcodeException,
  PaidBillException,
  UserWithProblemsInCodensaException,
  WithoutPendingBillsException
} from '@nequi/features/codensa/core/exceptions';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { OperationType, Transaction } from '@nequi/features/commons/core';
import { QrReaderService, TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  OriginMoneyControlComponent
} from '@nequi/features/money-management/presentation/controls';
import { BottomSheetController, ConfirmInfoPocketProps } from '@nequi/ui';

import { BillListOrganism } from '../../organisms/bill-list/bill-list.organism';
import { Scanner, SearchInfo } from '../interfaces';
import { CodensaConfig } from './home.config';
import { HomeService } from './home.service';
import { HomeViewModel } from './home.view-model';

@Component({
  selector: 'app-codensa-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
  providers: [HomeService],
})
export class HomePage extends TransactionBasePage implements OnInit {
  @ViewChild(OriginMoneyControlComponent) public originMoneyControl: OriginMoneyControlComponent;
  public config = CodensaConfig;
  public viewModel = new HomeViewModel();

  private factoryException = {};

  constructor(
    private readonly codensaInteractor: CodensaInteractor,
    private readonly homeService: HomeService,
    private readonly bottomSheetController: BottomSheetController,
    private readonly currencyPipe: CurrencyPipe,
    private readonly datePipe: DatePipe,
    public readonly qrReaderService: QrReaderService,
    private moduleConfigProvider: ModuleConfigProvider,
    injector: Injector
  ) {
    super(injector);
    this.setExceptionFactory();
  }

  public async ngOnInit() {
    const infoSearch = await this.homeService.getInfoSearch();

    this.viewModel.buildForm(infoSearch);
    this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey).then(() => {
      this.homeService.showCodensaWelcome(this.homeService.isWrongTime);
    });
  }

  public async onGetBills() {
    try {
      await this.loadingService.show();

      const bills: BillEntity[] = await this.codensaInteractor.getBills(
        await this.viewModel.getBillsData()
      );

      await this.loadingService.hide(true);

      this.homeService.setInfoSearch({
        accountNumber: this.viewModel.accountNumber.value,
        email: this.viewModel.email.value,
      } as SearchInfo);

      this.showBottomSheetBills(bills);
    } catch (error) {
      this.manageErrors(error);
    }
  }

  public async onScanCode() {
    const messages = this.translateProvider.get(this.config.i18n.barcodeReader);

    const scannerView: Scanner = {
      ...messages,
      showGalleryButton: this.config.barcodeReader.showGalleryButton,
    };

    this.openScanner(scannerView);
  }

  public goBack() {
    this.homeService.goBack();
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const billType = this.translateProvider.get(
      `${this.config.i18n.billType}.${this.viewModel.billInfo.type}`
    );

    const { title, subtitle, ...messages } = this.translateProvider.get(
      this.config.i18n.bottomSheet.confirm
    );

    return {
      title: title,
      subtitle: subtitle,
      items: this.viewModel.buildItems(messages, billType, this.currencyPipe, this.datePipe),
      confirmLabel: messages.button,
    };
  }

  protected async onTransactionFailed(error: CoreException, _transaction: Transaction<BillEntity>) {
    this.loadingService.hide(false);

    this.manageErrors(error);
  }

  protected async onTransactionSuccess(
    _response?: Record<string, string>,
    _transaction?: Transaction<BillEntity>
  ) {
    this.loadingService.hide(true);

    this.homeService.onShowCardModal(this.config.i18n.successPaymentAlert, () =>
      this.homeService.goBack()
    );
  }

  private setExceptionFactory() {
    this.factoryException = {
      [PaidBillException.name]: () => this.homeService.onShowError(this.config.i18n.paidBillAlert),
      [BillWithPaymentInTransitException.name]: () =>
        this.homeService.onShowError(this.config.i18n.billWithPaymentInTransit),
      [UserWithProblemsInCodensaException.name]: () =>
        this.homeService.onShowError(this.config.i18n.userWithProblemsInCodensa),
      [InvalidBarcodeException.name]: () => this.onInvalidBarcodeError(),
    };
  }

  private async showBottomSheetBills(bills: BillEntity[]) {
    const title = this.translateProvider.get(this.config.i18n.title);
    const componentProps = {
      title: title,
      items: bills,
      action: async (item: BillEntity) => {
        await this.onBillClicked(item);
      },
    };

    const bottomSheet = this.bottomSheetController.create({
      isAutoHeight: true,
      component: BillListOrganism,
      componentProps,
    });

    await bottomSheet.open();

    return bottomSheet.onDidDismiss();
  }

  private async onBillClicked(item: BillEntity) {
    this.viewModel.billInfo = item;

    this.makePayment({
      ...this.viewModel.billInfo,
      email: this.viewModel.email.value,
    });
  }

  private async makePayment(bill: BillEntity) {
    const transactionInfo = new Transaction<BillEntity>(OperationType.CODENSA_PAYMENT)
      .withAmount(bill.amount)
      .withPocket(this.viewModel.pocket.value)
      .withData(bill)
      .useRepository(CodensaRepository.name);

    super.executeTransaction<BillEntity>(transactionInfo);
  }

  private async openScanner(scannerView: Scanner) {
    try {
      const barcodeString = await this.qrReaderService.launch({
        content: scannerView?.content,
        imageName: scannerView?.imageName,
        title: scannerView?.title,
        showGalleryButton: scannerView?.showGalleryButton.toString(),
      });

      this.onReadBarcodeSuccess(barcodeString);
    } catch (error) {
      if (error !== this.config.barcodeReader.errors.goBackOnCamera) {
        this.manageBarcodeErrors(error);
      }
    }
  }

  private async onReadBarcodeSuccess(billBarcode: string) {
    try {
      const params: BarcodeEntity = await this.codensaInteractor.processBarcode(billBarcode);

      this.viewModel.setAccountNumber(params);
    } catch (error) {
      this.manageErrors(error);
    }
  }

  private async manageBarcodeErrors(error: string) {
    const message = this.translateProvider.get(`${this.config.i18n.barcodeError}.${error}`);

    await this.toastService.showError(message);
  }

  private async manageErrors(error: CoreException) {
    if (error.constructor === WithoutPendingBillsException) {
      await this.onWithoutPendingBillsError();

      return;
    }

    await this.loadingService.hide(false);

    if (this.factoryException[error.constructor.name]) {
      this.factoryException[error.constructor.name]();

      return;
    }

    throw error;
  }

  private async onWithoutPendingBillsError() {
    await this.loadingService.hide(true);

    this.homeService.onShowCardModal(this.config.i18n.withoutPendingBillsError, () =>
      this.homeService.goBack()
    );
  }

  private async onInvalidBarcodeError() {
    const message = this.translateProvider.get(this.config.i18n.invalidBarcodeError);

    await this.toastService.showError(message);

    this.viewModel.setAccountNumber();
  }
}
