import { Component, Input } from '@angular/core';
import { BillEntity } from '@nequi/features/codensa/core/entities';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { BottomSheetController } from '@nequi/ui';

import { BillListProps } from './entities/bill-list-props.interface';

@Component({
  selector: 'app-bill-list',
  templateUrl: './bill-list.organism.html',
  styleUrls: ['./bill-list.organism.scss'],
})
export class BillListOrganism implements BillListProps {
  @Input() public items: BillEntity[];
  @Input() public title: string;
  @Input() public action: (item: BillEntity) => Promise<void>;

  constructor(
    public bottomSheetController: BottomSheetController,
    public translateProvider: TranslateProvider
  ) {}

  public async sendBill(item: BillEntity) {
    await this.action(item);
    this.bottomSheetController.dismiss();
  }

  public hide() {
    this.bottomSheetController.dismiss();
  }
}
